@import url(https://fonts.googleapis.com/css?family=Roboto+Mono&subset=greek);
body {
  margin: 0;
  font-family: 'Roboto Mono',sans-serif !important ;

}
.primary{
  background-color: #161c51;
  padding:5px 20px;
  color:white;
  border:none;
  }


.layout{
    margin:0 100px;
}

.header{
    background-color: #161c51   ;
    padding:10px;
    border-bottom-right-radius:25px;
    border-bottom-left-radius: 25px;
}
.logo{
    font-size:30px;
    color:rgba(255,255,255, 0.716);
    margin:0;
    cursor:pointer;

    }
.username{
    font-size: 18px;
    color:white;
    }
.content{
    height: 85vh;
    box-shadow: 0 0 3px grey;
    margin-top: 20px;
    border-top-right-radius: 25px;
    border-top-left-radius: 25px;
    padding:15px;

}
.spinner{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50% , -50%);
            transform: translate(-50% , -50%);
}

.ant-spin-dot-item{
    background-color: gray !important;
}
@media  screen and (max-width:700px) {
    .layout{
        margin:0 15px;
    }
}
.filter{
    box-shadow: 0 0 2px gray;
    padding: 15px 20px;
    border-radius: 5px;
}

.transaction-form label{
    color: rgba(0, 0, 0, 0.77) !important;
}
.transaction-form input{
    border: 1px solid gray;
    border-bottom: 1px solid gray;
    box-shadow: none !important;
}
.transaction-form input:focus{
    border: 1px solid gray;
    border-bottom: 1px solid gray !important;
    box-shadow: none !important;
}

.ant-select-selector{
    border: 1px solid gray !important;
}
.ant-select-selector:focus{
    box-shadow:none !important;
    outline: none !important;
}

.view-switch{
    border: 1px solid rgba(0, 0, 0, 0.71);
    border-radius: 3px;
    padding: 5px 10px;
}


.anticon svg{
    font-size: 20px;
    cursor: pointer;
}

.active-icon{
    color: black;
    
}
.inactive-icon{
    color: gray;

}
.ant-picker-range{
    border: 1px solid black !important;
}

@media screen and (max-width:600px){
    .filter{
        overflow-x: scroll;
    }
}
.transactions-count{
    box-shadow: 0 0 2px rgb(132, 131, 131);
    padding: 15px;
    border-radius: 10px;
    color: rgb(56, 55, 55) !important;
}

.analytics h4{
    font-size: 20px;
    color: gray !important;
    font-weight: 600;
}
.analytics h5{
    font-size: 16px;
    color: gray !important;
}

.category-card{
    padding: 5px 20px;
    box-shadow: 0 0 2px gray;
    margin-top: 15px;
    border-radius: 5px;
}
.register,.login{
    height:100vh;
    display:flex;
    align-items:center;
    justify-content:center;
    background-image: linear-gradient(to top, #fbc2eb 0%, #a6c1ee 100%);

}

.lottie{
    height:400px;
    }
.register input,.login input  {
    background-color: transparent;
    border:none;
    border-bottom:1px solid rgba(137,137,137,0.696);
    color:black;

     
}
.input:focus{
  outline:none !important;
  box-shadow:none !important;
}
label,a{
    color:rgba(255, 255, 255, 0.39);

}
.register h1{
    font-size: 25px;

}
